import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  DatePicker,
  Input,
  message,
  Checkbox
} from 'antd'
import style from './style/Form.scss'
import {API_SHIPPEDORDERS_BUSINESSKEYVALUES, API_SHOPINFOBYSHOPCODE_KEYVALUES, API_MERCHANTPRIORITYCHOICE_KEYVALUES, API_SHIPPEDORDERS_EXPRESSKEYVALUES, API_PRODUCT_GETPRODUCTKEYVALUES} from 'api'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {CsxSelect, CsxMultipleSearch, CsxSearch} from 'Form'
import {NOSENDFLAG} from 'Mock'
import moment from 'moment'
const {RangePicker} = DatePicker

@connect(
  state => ({...state.outWarehouseListSell, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  componentWillMount() {
    this.initDateOpt()
  }
  // 设置默认的时间  前一月 - 当天
  initDateOpt(values) {
    const {update} = this.props
    const preMonth = moment().subtract(1, 'M').startOf('day')
    update({searchParams: {initDate: [preMonth, moment().endOf('day')]}})
  }

  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }
  // 查询数据
  query = () => {
    const {handleGetList, searchParams, size} = this.props
    let reqObj = {...searchParams}
    const {initDate = []} = searchParams
    // 判断创建时时间是否为空
    if (!initDate || (Array.isArray(initDate) && initDate.length < 2)) {
      message.error('请选择创建时间后搜索')
    }

    handleGetList({searchParams: {...reqObj, page: 1, size}})
  }
  // 重置
  reset = () => {
    const {update} = this.props
    update({searchParams: {}})
     // 重置时间
    this.initDateOpt()
  }
  render() {
    const {
      searchParams: {
        initDate,
        receivePhoneNumber,
        customerName,
        businessType,
        // status,
        shopType,
        distributeShortageFlag,
        orderCode,
        shopCode,
        deliveryCode,
        outPrintTimes,
        productCode,
        productName,
        planDate,
        noSendFlag,
        stationCode,
        initDate2,
		entruckingFlag
      },
      userLoation
    } = this.props
    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
          // gutter={16}
        >
          <Col span={6}>
            <div className='commonInput'>
              <label>订单查询：</label>
              <div className='box'>
                <Input placeholder='支持各种单号查询' value={orderCode} onChange={(e) => {
                  this.onInputChange({orderCode: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_SHIPPEDORDERS_BUSINESSKEYVALUES}
              label='业务类型'
              searchParams={{orderType: 'S'}}
              selectItem={{data: businessType, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({businessType: value})
              }}
            />
          </Col>
          {/* <Col span={6}>
            <CsxSelect
              data={NOSENDFLAG}
              label='补单空出'
              selectItem={{data: noSendFlag, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({noSendFlag: value})
              }}
            />
          </Col> */}
          {/* <Col span={6}>
            <CsxSelect
              dateApi={API_SHIPPEDORDERS_STATUSKEYVALUES}
              label='出库状态'
              selectItem={{data: status, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({status: value})
              }}
            />
          </Col> */}
          <Col span={6}>
            <CsxMultipleSearch
              dateApi={API_SHOPINFOBYSHOPCODE_KEYVALUES}
              label='门店'
              searchParamsKey='shopCode'
              selectItem={{data: shopCode, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({shopCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_MERCHANTPRIORITYCHOICE_KEYVALUES}
              label='门店类型'
              selectItem={{data: shopType, key: 'merchantType', valueKey: 'merchantType'}}
              onChangeSelect={(value) => {
                this.onInputChange({shopType: value})
              }}
            />
          </Col>
          <Col span={6}>
            <div className='commonInput'>
              <label>客户：</label>
              <div className='box'>
                <Input value={customerName} onChange={(e) => {
                  this.onInputChange({customerName: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col span={6}>
            <div className='commonInput'>
              <label>收货人电话：</label>
              <div className='box'>
                <Input value={receivePhoneNumber} onChange={(e) => {
                  this.onInputChange({receivePhoneNumber: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col span={6}>
            <div className='commonInput'>
              <label>站点编号：</label>
              <div className='box'>
                <Input value={stationCode} onChange={(e) => {
                  this.onInputChange({stationCode: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_SHIPPEDORDERS_EXPRESSKEYVALUES}
              label='配送方式'
              selectItem={{data: deliveryCode, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({deliveryCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSearch
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              label='商品编码'
              searchParamsKey='productCode'
              searchParams={{warehouseCode: userLoation.value}}
              selectItem={{data: productCode, key: 'key', valueKey: ['key', 'value']}}
              onSelect={(value) => {
                this.onInputChange({productCode: value})
              }}
              onChangeSelect={(value) => {
                this.onInputChange({productCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSearch
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              label='商品名称'
              searchParamsKey='productName'
              searchParams={{warehouseCode: userLoation.value}}
              selectItem={{data: productName, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value, n) => {
                this.onInputChange({productName: value})
              }}
              onSelect={(value) => {
                this.onInputChange({productName: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              label='已打印出库单'
              data={[{key: '', value: '全部'}, {key: true, value: '是'}, {key: false, value: '否'}]}
              selectItem={{data: outPrintTimes, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({outPrintTimes: value})
              }}
            />
          </Col>
          <Col span={6}>
            <div className='commonInput'>
              <label>预计发货日期：</label>
              <div className='box' style={{marginRight: 0}}>
                <DatePicker onChange={(moment, string) => {
                  this.onInputChange({planDate: moment})
                }}
                  format={'YYYY-MM-DD'}
                  value={planDate}
                />
              </div>
            </div>
          </Col>

          <Col span={8}>
            <div className='commonInput'>
              <label>创建时间：</label>
              <div className='box' style={{marginRight: 0}}>
                <RangePicker onChange={(moment, string) => {
                  this.onInputChange({initDate: moment})
                }}
                  format={'YYYY-MM-DD'}
                  value={initDate}
                  allowClear={false}
                />
              </div>
            </div>
          </Col>
          <Col span={8}>
            <div className='commonInput'>
              <label>出库时间：</label>
              <div className='box' style={{marginRight: 0}}>
                <RangePicker onChange={(moment, string) => {
                  this.onInputChange({initDate2: moment})
                }}
                  format={'YYYY-MM-DD'}
                  value={initDate2}
                />
              </div>
            </div>
          </Col>
          <Col span={8}>
            <Checkbox checked={distributeShortageFlag ? true : false} onChange={(e) => {
              this.onInputChange({distributeShortageFlag: e.target.checked})
            }}>分配短缺</Checkbox>
            <Checkbox checked={entruckingFlag ? true : false} onChange={(e) => {
              this.onInputChange({entruckingFlag: e.target.checked})
            }}>是否装车</Checkbox>
          </Col>
          <Col span={24}>
            <Row type="flex" justify="end">
              <Button type="primary" className={style.mr10} onClick={this.query} >查询</Button>
              <Button type="primary" className={style.mr10} onClick={this.reset} >重置</Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



