import React, {PureComponent} from 'react'
import {
  message,
  Button,
  Tabs,
  Modal
} from 'antd'
import style from './style/Tool.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {setVariant} from 'Variant'
const {TabPane} = Tabs
const confirm = Modal.confirm
import {QYG, SCTYPE, BBC} from 'Mock'
import moment from 'moment'
import {Import} from 'HOC'
import {API_PACKAGE_DELIVERYPRODUCT_IMPORT, API_SHIPPEDORDERS_PRODUCT_IMPORT} from 'api'
import {getNowPageId} from '../../global/components/menuData'

@connect(
  state => ({...state.outWarehouseListSell}),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  /* 打印预览 */
  handleBrowse = () => {
    let {selectedRows} = this.props
    if (!selectedRows.length) {
      message.error('请勾选订单')
      return
    }
    // selectedRows = selectedRows.filter((o) => o.status == 6 || o.status == 7 || o.status == 8)
    // if (!selectedRows.length) {
    //   message.error('出库后才能打印出库单')
    //   return
    // }

    let type = 1 // 默认wms的打印
    let pass = true
    let nowType = ''
    //判断是否是同一种的类型
    for (let i of Object.keys(selectedRows)) {
      if (i == 0) {
        nowType = selectedRows[i].businessType
      } else {
        if (nowType != selectedRows[i].businessType) {
          pass = false
          break
        }
      }
    }
    if (!pass) {
      message.error('请选择同一种业务类型')
      return
    }
    // 判断是否是商超的类型，需要选择是否过滤为0的商品
    new Promise((resolve, reject) => {
      if (SCTYPE.indexOf(nowType) != -1) {
        confirm({
          title: '请选择',
          content: '是否过滤出库数量0的商品？',
          cancelText: '否',
          okText: '是',
          onOk() {
            resolve(true)
          },
          onCancel() {
            resolve(false)
          }
        })
      } else {
        resolve(false)
      }
    }).then(r => {
      setVariant('wms-print-filterZero', r)
      // 判断类型是跳自己的打印还是跳企业购的打印 1: wms , 2 企业购
      QYG.indexOf(nowType) != -1 ? type = 2 : type = 1
      if (type == 1) {
        let ids = []
        selectedRows.map((o) => {
          ids.push(o.id)
        })
        setVariant('wms/outWarehouseList', ids)
        console.log('ids is', ids)
        window.open('/wms/outWarehouseList/Print', 'newWindow', 'height=500, width=1300, top=0, left=0, toolbar=no, menubar=no, scrollbars=no, resizable=no, location=no, status=no')
      } else {
        let orderList = [],
          reqData = []
        selectedRows.map((o) => {
          orderList.push(o.orderCode)
          reqData.push({
            id: o.id
          })
        })
        setVariant('orderList', orderList)
        const {handleUpdatePrintNum} = this.props
        handleUpdatePrintNum({data: reqData})
        window.open('/sms/outingListBatch', 'newWindow', 'height=500, width=1300, top=0, left=0, toolbar=no, menubar=no, scrollbars=no, resizable=no, location=no, status=no')
      }
    })
  }

  componentDidMount() {
    //权限配置
    const {GetButtonPower} = this.props
    const MEUN = JSON.parse(localStorage.getItem('b2bUserMenu'))
    const ID = getNowPageId(MEUN, window.location.pathname)
    console.log(ID, 444)
    GetButtonPower({urlParams: [ID]})
  }

  handleBrowse2 = () => {
    const {searchParams, size, page} = this.props
    let reqObj = {...searchParams, page: page || 1, size}
    if (!reqObj.businessType || !reqObj.planDate) {
      message.error('请先选择业务类型跟预计发货日期')
      return
    }
    if (reqObj.planDate) {
      reqObj.planDate = moment(reqObj.planDate).format('YYYY-MM-DD')
    }
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        i === 'productName' ? reqObj[i] = reqObj[i].children : reqObj[i] = reqObj[i].value
      }
    }
    if (reqObj.initDate && reqObj.initDate.length) {
      reqObj.createTimeStart = moment(reqObj.initDate[0]).format('YYYY-MM-DD')
      reqObj.createTimeEnd = moment(reqObj.initDate[1]).format('YYYY-MM-DD')
    }
    if (reqObj.initDate2 && reqObj.initDate2.length) {
      reqObj.sendDateStart = moment(reqObj.initDate2[0]).format('YYYY-MM-DD')
      reqObj.sendDateEnd = moment(reqObj.initDate2[1]).format('YYYY-MM-DD')
    }
    delete reqObj.initDate2
    delete reqObj.initDate

    setVariant('wms/outWarehouseList', JSON.stringify(reqObj))
    window.open('/wms/outWarehouseList/Print2', 'newWindow', 'height=500, width=1300, top=0, left=0, toolbar=no, menubar=no, scrollbars=no, resizable=no, location=no, status=no')
  }
  // 导出
  handleExport = () => {
    const {handleExport, searchParams, tableActiveKey, size, page} = this.props
    handleExport({searchParams: {orderType: 'S', status: tableActiveKey, size, page, ...searchParams, productName: searchParams.productName ? searchParams.productName.children : ''}}, () => {
      message.success('导出成功')
    })
  }
  // 出库
  handleOut = () => {
    let {selectedRows, handleOut, handleGetList, searchParams, size, page, tableActiveKey} = this.props
    if (!selectedRows.length) {
      message.error('请勾选订单')
      return
    }
    let selectedRows2 = []
    selectedRows2 = selectedRows.filter((o) => o.businessType == '18')
    if (!selectedRows2.length) {
      message.error('只有“客户配送”的订单才可出库')
      return
    }
    let data = []
    selectedRows2.map((o) => {
      data.push({
        id: o.id
      })
    })
    if (selectedRows.length != selectedRows2.length) {
      confirm({
        title: '温馨提示',
        content: '只有“客户配送”的订单才可出库，其余将不被操作',
        okText: '确认',
        width: 520,
        cancelText: '取消',
        onOk() {
          handleOut({data}, () => {
            message.success('操作成功')
            let reqObj = {...searchParams}
            handleGetList({searchParams: {...reqObj, page, size, status: tableActiveKey}}, true)
          }, () => {
            let reqObj = {...searchParams}
            handleGetList({searchParams: {...reqObj, page, size, status: tableActiveKey}}, true)
          })
        }
      })
    } else {
      handleOut({data}, () => {
        message.success('操作成功')
        let reqObj = {...searchParams}
        handleGetList({searchParams: {...reqObj, page, size, status: tableActiveKey}}, true)
      }, () => {
        let reqObj = {...searchParams}
        handleGetList({searchParams: {...reqObj, page, size, status: tableActiveKey}}, true)
      })
    }
  }
    // 查询数据
  query = (tableActiveKey) => {
    const {handleGetList, searchParams, size, update} = this.props
    let reqObj = {...searchParams}
    update({tableActiveKey})
    handleGetList({searchParams: {...reqObj, page: 1, status: tableActiveKey, size}}, true)
  }
  /* 下载模版 */
  handleDownLoad = (key) => {
    const downloadData = JSON.parse(localStorage.getItem('b2bWmsDownload'))
    console.log(downloadData, 111)
    window.open(downloadData[key].downloadUrl)
  }

  /* 导入成功 */
  handleImportSuccess = res => {
    res && res.message === 'ok' && message.success('导入成功')
    const {handleGetList, searchParams, size} = this.props
    let reqObj = {...searchParams}
    handleGetList({searchParams: {...reqObj, page: 1, size}})
  }
      /* 报错导出 */
  handleExprotError = () => {
  }

  // BBC取消订单发货
  BBCCancelOrder = () => {
    const {BBCCancelOrder, selectedRows, searchParams, handleGetList, page, size, tableActiveKey} = this.props
    let pass = true
    let arr = []
    // let nowType = ''
    //判断是否是同一种的类型
    for (let i of Object.keys(selectedRows)) {
      // if (i == 0) {
      //   nowType = selectedRows[i].businessType
      // } else {
      //   if (nowType != selectedRows[i].businessType) {
      //     pass = false
      //     break
      //   }
      // }
      if (BBC.indexOf(selectedRows[i].businessType) == -1) {
        pass = false
      }
      arr.push(selectedRows[i].orderCode)
    }
    if (!pass) {
      message.error('请选择BBC业务类型')
      return
    }
    BBCCancelOrder({data: {orderCode: arr.toString()}}, () => {
      message.success('操作成功')
      let reqObj = {...searchParams}
      handleGetList({searchParams: {...reqObj, page, size, status: tableActiveKey}})
    }, () => {
      let reqObj = {...searchParams}
      handleGetList({searchParams: {...reqObj, page, size, status: tableActiveKey}})
    })
  }

    //批量审核
  batchReview=() => {
    const {handleCyorDerchek, handleExportShortage, selectedRows} = this.props
    let array = []
    selectedRows.map(o => array.push(o.linkOrderCode))
    if (!array.length || array.indexOf('') != -1) {
      message.error('请选择差异待处理订单进行审核')
      return
    }
    handleCyorDerchek({data: {shippedOrderCode: array.join()}}, (data) => {
      console.log(data.result.hasError, 333)
      if (!data.result.hasError) {
        message.success(data.result.message)
      } else {
        confirm({
          content: data.result.message,
          cancelText: '取消',
          okText: '缺货导出',
          onOk() {
            data.result.shortageList ?
            handleExportShortage({searchParams: {shortageList: data.result.shortageList}}) : message.error('暂无缺货导出')
          }
        })
      }
      const {handleGetList, searchParams, page, size} = this.props
      handleGetList({searchParams: {page, size, ...searchParams}})
    })
  }

  render() {
    const {tableActiveKey, tab0, tab1, tab2, tab3, tab4, tab5, tab6, tab7, tab8, tab9, tab10, ImportGoodsPower, BBCCancelOrderPower, outWarehouseListSellExport, outWarehouseListSellPrint, outWarehouseListSellmport, outWareBatchReview} = this.props
    return (
      <div className={style['table-tool']} style={{paddingBottom: 0}}>
        {
          outWarehouseListSellExport && <Button type="primary" className={style.btn} onClick={this.handleExport}>导出</Button>
        }
        {
          outWarehouseListSellPrint && <Button type="primary" className={style.btn} onClick={this.handleBrowse}>打印出库单</Button>
        }
        <Button type="primary" className={style.btn} onClick={this.handleBrowse2}>汇总分拣单打印</Button>
        {
          ImportGoodsPower && <React.Fragment>
            <Button type="primary" className={style.btn} onClick={() => {
              this.handleDownLoad('T12')
            }}>发货模板下载</Button>
            <Import
              action={{api: API_PACKAGE_DELIVERYPRODUCT_IMPORT}}
              onOk={this.handleImportSuccess}
              onExprotError={this.handleExprotError}
              beforeImport={() => {
                const {update} = this.props
                update({tableLoading: true})
              }}
              onResponse={() => {
                const {update} = this.props
                console.log(111)
                update({tableLoading: false})
              }}
            >
              <Button type="primary" className={style.btn}>导入包裹商品</Button>
            </Import>
          </React.Fragment>
        }
        {
          tableActiveKey == 0 && BBCCancelOrderPower && <Button type="primary" className={style.btn} onClick={this.BBCCancelOrder}>BBC订单取消发货</Button>
        }
        {
          outWarehouseListSellmport && <React.Fragment>
            <Button type="primary" className={style.btn} onClick={() => {
              this.handleDownLoad('T14')
            }}>出库单模板下载</Button>
                        <Import
              action={{api: API_SHIPPEDORDERS_PRODUCT_IMPORT}}
              onOk={this.handleImportSuccess}
              onExprotError={this.handleExprotError}
              beforeImport={() => {
                const {update} = this.props
                update({tableLoading: true})
              }}
              onResponse={() => {
                const {update} = this.props
                console.log(111)
                update({tableLoading: false})
              }}
            >
              <Button type="primary" className={style.btn}>出库单导入</Button>
            </Import>
          </React.Fragment>
        }
        {/* <Button type="primary" className={style.btn} onClick={this.handleOut}>出库</Button> */}
        {
         outWareBatchReview && tableActiveKey == 7 && <Button type="primary" className={style.btn} onClick={this.batchReview}>批量审核</Button>
        }
        <Tabs onChange={this.query} activeKey={tableActiveKey} >
          <TabPane tab={`初始(${tab0 || 0})`} key="0"></TabPane>
          <TabPane tab={`已集波(${tab1 || 0})`} key="1"></TabPane>
          <TabPane tab={`分配中(${tab2 || 0})`} key="2"></TabPane>
          <TabPane tab={`已分配(${tab3 || 0})`} key="3"></TabPane>
          <TabPane tab={`拣货中(${tab4 || 0})`} key="4"></TabPane>
          <TabPane tab={`拣货完成(${tab5 || 0})`} key="5"></TabPane>
          <TabPane tab={`已发货(${tab6 || 0})`} key="6"></TabPane>
          <TabPane tab={`差异待处理(${tab7 || 0})`} key="7"></TabPane>
          <TabPane tab={`已签收(${tab8 || 0})`} key="8"></TabPane>
          <TabPane tab={`已取消(${tab9 || 0})`} key="9"></TabPane>
          <TabPane tab={`已装车(${tab10 || 0})`} key="10"></TabPane>
        </Tabs>
      </div>
    )
  }
}



